/**
 * Shared Inspector Controls configuration for form wrapper
 * Provides consistent form settings interface
 */
import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextControl, TextareaControl } from '@wordpress/components';

export const FormWrapperInspectorControls = ( {
	attributes,
	setAttributes,
} ) => {
	const { formId, successMessage, errorMessage } = attributes;

	return (
		<InspectorControls>
			<PanelBody
				title={ __( 'Form Settings', 'form-builder-block' ) }
				initialOpen={ true }
			>
				<TextControl
					label={ __( 'Form ID', 'form-builder-block' ) }
					value={ formId }
					onChange={ ( value ) => setAttributes( { formId: value } ) }
					help={ __(
						'Unique identifier for this form (optional)',
						'form-builder-block'
					) }
					placeholder={ __(
						'e.g., contact-form',
						'form-builder-block'
					) }
				/>
			</PanelBody>
			<PanelBody
				title={ __( 'Form Messages', 'form-builder-block' ) }
				initialOpen={ false }
			>
				<TextareaControl
					label={ __( 'Success Message', 'form-builder-block' ) }
					value={ successMessage }
					onChange={ ( value ) =>
						setAttributes( { successMessage: value } )
					}
					help={ __(
						'Message displayed when form is successfully submitted',
						'form-builder-block'
					) }
					rows={ 3 }
				/>
				<TextareaControl
					label={ __( 'Error Message', 'form-builder-block' ) }
					value={ errorMessage }
					onChange={ ( value ) =>
						setAttributes( { errorMessage: value } )
					}
					help={ __(
						'Message displayed when form submission fails',
						'form-builder-block'
					) }
					rows={ 3 }
				/>
			</PanelBody>
		</InspectorControls>
	);
};
